import React, { useState } from 'react'
import { Container, Grid, Button, makeStyles, Typography } from '@material-ui/core'
import Header from "../components/Header/Header";

const useStyles = makeStyles(() => ({
    container: {
        padding: '88px 20px 20px'
    }
}))

export default function BluetoothPage() {
    const classes = useStyles()
    const [error, setError] = useState(null)
    const [device, setDevice] = useState(null)
    const [open, setOpen] = useState(false);

    const handleOpenNavBar = () => {
        setOpen(!open);
    };
    // function handleBatteryLevelChanged(event) {
    //     let batteryLevel = event.target.value.getUint8(0);
    //     console.log('Battery percentage is ' + batteryLevel);
    //     setError('Battery percentage is ' + batteryLevel)
    // }
    const onDisconnected = (event) => {
        let device = event.target;
        console.log(`device ${device.name} is disconnected`);
        setError(`device ${device.name} is disconnected`)
    }
    const findDevice = () => {
        console.log('find device')
        navigator.bluetooth.requestDevice({ acceptAllDevices: true }).then(device => {
            setDevice(device.name)
            console.log('device.name', device.name)
            device.addEventListener('gattservertdisconnected', onDisconnected)
            return device.gatt.connect()
            // Do something with the device.
        }).catch(function (error) {
            console.log("Something went wrong. " + error);
            setError("Something went wrong. " + error)
        });
        // navigator.bluetooth.requestDevice({ acceptAllDevices: true }) //filter: [{services: ['battery_service'] }]
        //     .then(device => {
        //         setDevice(device.name)
        //         console.log('device.name', device.name)
        //         device.addEventListener('gattservertdisconnected', onDisconnected)
        //         return device.gatt.connect()
        //     })
        //     .then(server => {
        //         // Getting Battery Service...
        //         return server.getPrimaryService('battery_service');
        //     })
        //     .then(service => {
        //         // Getting Battery Level Characteristic...
        //         return service.getCharacteristic('battery_level');
        //     })
        //     .then(characteristic => characteristic.startNotifications())
        //     .then(characteristic => {
        //         // Set up event listener for when characteristic value changes.
        //         characteristic.addEventListener('characteristicvaluechanged',
        //             handleBatteryLevelChanged);
        //             console.log('Notifications have been started')
        //         setError('Notifications have been started')
        //         // Reading Battery Level...
        //         return characteristic.readValue();
        //     })
        //     .catch(error => { setError(''+error); console.log(error); });
    }

    return (
        <div id="st-container" className={open?"st-container st-effect-1 st-menu-open":"wrapper dashboard-wrapper st-container"}>
            <Header
                header="Bluetooth Sandbox"
                openNavBar={handleOpenNavBar}
                open={open} />
            <Container maxWidth='sm' className={classes.container}>
                <Grid container spacing={0}>
                    <Grid item xs={12}>
                        <Button
                            variant='contained'
                            color='primary'
                            onClick={findDevice}
                            style={{marginBottom: '16px'}}
                        >Find Bluetooth Device</Button>
                        {device && <Typography variant='body2'>
                            Device name : {device}
                        </Typography>}
                        {error && <Typography variant='body2'>
                            Note : {error}
                        </Typography>}
                    </Grid>
                </Grid>
        </Container>
    </div>
    )
}
